import { Component, OnInit } from '@angular/core';
import { TestMB } from '../../../shared/test-manage-batch';
import { TestService } from '../../services/test.service';

@Component({
  selector: 'app-test-manage-batch',
  templateUrl: './test-manage-batch.component.html',
  styleUrls: ['./test-manage-batch.component.css']
})
export class TestManageBatchComponent implements OnInit {

  testList: TestMB[];
  constructor(private ts: TestService) { 

  }

  ngOnInit() {
    this.ts.getAllTests().subscribe(
      data => this.testList = data,
      err => console.log(err.error)
    )
  }

}
