import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import {TestMB} from '../../shared/test-manage-batch';

@Injectable({
  providedIn: 'root'
})
export class TestService {

  readonly url: string = "http://localhost:8080/Project2/TestHomeServlet";

  constructor(private http:HttpClient) {}

  getAllTests(): Observable<TestMB[]> {
    return this.http.get<TestMB[]>(this.url);
  }

  
}
